body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

img {
  clear: none;
}


.c-list {
    overflow: hidden;
    /* float: right; */
    list-style: none;
    /* background: #fff; */
}

.c-list .list-item {
    width: 196px;
    height: 199px;
    margin: 10px;
    /* border-top: 1px solid #ddd;
    border-right: 1px solid #ddd; */
    text-align: center;
    float: left;
    background:#e3e3e3;
    border-radius:10px
}

ul, menu, dir {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 40px;
}

.c-list .list-item .p-name {
    display: inline-block;
    width: 160px;
    height: 34px;
    line-height: 34px;
    font-size: 12px;
    color: #666;
    margin-top: 7px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

a {
    color: #666;
    text-decoration: none;
}

.c-list .list-item .p-price {
    font-size: 16px;
    color: #fa2846;
}

.c-list .list-item .p-img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    margin-top: 20px;
}
.App-logo {
    -webkit-animation: App-logo-spin infinite 10s linear;
            animation: App-logo-spin infinite 10s linear;
    height: 60px;
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.logo {
  width: 260px;
  height: 60px;
  margin: 0;
  float: left;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}

.ant-layout-sider-collapsed .anticon {
    font-size: 16px;
}

.ant-layout-sider-collapsed .nav-text {
    display: none;
}

.contentDiv{
	height:510px;
	width:100%;
	text-align:center;
	vertical-align:middle;
	background-color:#e3e3e3;
	bolder:1px;
}

.linkClass{
	margin:2px;
}


.c-img-box{
        display:inline-block;
        width: 100%;;
        height: 500px;
        background: #f7f6f5;
        position: relative;
        
        }
		
.img-hold {
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
}
img{
	width:100%;
	height:100%;
}
&.img-animate{
		-webkit-transition: opacity 0.5s;
		transition: opacity 0.5s;
	}
        
.img-cover{
	background: url('https://d.2dfire.com/om/images/menulist/7deb58da.default.png') no-repeat center/300px;
	background-size:contain;
	background-color:#f0f0f0;
}
        
.img-cover,
.img-hold,
.img-hide{
	position: absolute;
	width: 100%;
	height: 100%;
	top:0;
	left:0;
}
        
.img-hide{
	display:none;
	opacity: 0;
}
        

