

.c-list {
    overflow: hidden;
    /* float: right; */
    list-style: none;
    /* background: #fff; */
}

.c-list .list-item {
    width: 196px;
    height: 199px;
    margin: 10px;
    /* border-top: 1px solid #ddd;
    border-right: 1px solid #ddd; */
    text-align: center;
    float: left;
    background:#e3e3e3;
    border-radius:10px
}

ul, menu, dir {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 40px;
}

.c-list .list-item .p-name {
    display: inline-block;
    width: 160px;
    height: 34px;
    line-height: 34px;
    font-size: 12px;
    color: #666;
    margin-top: 7px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

a {
    color: #666;
    text-decoration: none;
}

.c-list .list-item .p-price {
    font-size: 16px;
    color: #fa2846;
}

.c-list .list-item .p-img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    margin-top: 20px;
}